import { Component, h, Element, State, Prop } from '@stencil/core';
import { ButtonSize } from '../../atui-button/atui-button';

/**
 * @category Navigation
 * @description A sidebar trigger component for the sidebar.
 */

@Component({
    tag: 'atui-sidebar-trigger',
    shadow: false,
})
export class AtuiSidebarTriggerComponent {
    @Element() el: HTMLElement;
    @State() isOpen: boolean = false;

    /**
     * Size of the button
     */
    @Prop()
    size: ButtonSize = 'lg';

    private provider: HTMLAtuiSidebarElement;

    private async updateIsOpen() {
        if (this.provider && typeof this.provider.getIsOpen === 'function') {
            this.isOpen = await this.provider.getIsOpen();
        }
    }

    private async toggleSidebar(): Promise<void> {
        if (
            this.provider &&
            typeof this.provider.toggleSidebar === 'function'
        ) {
            await this.provider.toggleSidebar();
            await this.updateIsOpen();
        }
    }

    async componentDidLoad() {
        this.provider = this.el.closest(
            'atui-sidebar',
        ) as HTMLAtuiSidebarElement;
        await this.updateIsOpen();
    }

    render() {
        return (
            <atui-button
                type="secondary"
                size={this.size}
                data-name="sidebar-trigger"
                icon={this.isOpen ? 'menu_open' : 'menu'}
                onClick={() => this.toggleSidebar()}
            ></atui-button>
        );
    }
}
